import { Injectable } from '@angular/core';
import { CommonVariables } from 'src/app/shared/commmon';
import { HttpClient } from '@angular/common/http';
import { CommonResponse } from 'src/app/shared/commmon/ServiceClass';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class SetFoodService {

  constructor(private util:CommonVariables,private http:HttpClient) { }
  //get customer data for dietitian
  GetCustomerDataForDietitian(CustomerID){
    let url=this.util.GetCustomerDetailsForDietitian+CustomerID;
    return this.http.get<CommonResponse>(url).pipe(map((response: CommonResponse) => response));
  }
  //get all ready to eat food items
  GetAllReadyToFoods(){
    let url=this.util.GetAllReadyToEatableFoods;
    return this.http.get<CommonResponse>(url).pipe(map((response: CommonResponse) => response));  
  }
  //get all meal types
  GetAllMealTypes(){
    let url=this.util.GetMealTypes;
    return this.http.get<CommonResponse>(url).pipe(map((response: CommonResponse) => response));
  }
  //save customer diet
  SaveCustomerDiet(body){
    let url=this.util.SaveCustomerDiet;
    return this.http.post<CommonResponse>(url,body).pipe(map((response:CommonResponse)=>response));
  }
  //get predefined food
  GetPredefinedDishes(MealTypeID){
    let url=this.util.GetDishByMealType+MealTypeID;
    return this.http.get<CommonResponse>(url).pipe(map((response:CommonResponse)=>{return response;}));
  }
}
