import { Component, OnInit, Inject } from '@angular/core';
import { SetFoodService } from '../set-food/set-food.service';
import { MAT_BOTTOM_SHEET_DATA, MatBottomSheetRef } from '@angular/material';
import { CommonVariables } from 'src/app/shared/commmon';

@Component({
  selector: 'app-pre-defined-dish',
  templateUrl: './pre-defined-dish.component.html',
  styleUrls: ['./pre-defined-dish.component.scss']
})
export class PreDefinedDishComponent implements OnInit {
  Dishes = [];
  MealTypeName = '';
  MealTypeID = 0;
  constructor(private service: SetFoodService, @Inject(MAT_BOTTOM_SHEET_DATA) public data: any,
  private bottomSheetRef: MatBottomSheetRef<PreDefinedDishComponent>, private util: CommonVariables) {
      this.MealTypeName = data.MealTypeName;
      this.MealTypeID = data.MealTypeID;
      this.GetDishByMealType(data.MealTypeID);
  }
  // GET DISHES
  GetDishByMealType(MealTypeID) {
    this.service.GetPredefinedDishes(MealTypeID).subscribe(Response => {
      if (Response.Status === true) {
        this.Dishes = Response.Data;
      }
    });
  }
  // Add dish
  AddDish(DishID, Title) {
    this.bottomSheetRef.dismiss({'MealTypeID': this.MealTypeID, 'DishID': DishID, 'Title': Title});
  }
  // get images
  GetDishImage(Images) {
    let FinalUrl = '';
    if (Images.length > 0) {
      FinalUrl = this.util.base_url + Images[0].FileLocation;
    } else {
      FinalUrl = 'assets/images/dish-1.jpg';
    }
    return FinalUrl;
  }
  ngOnInit() {
  }

}
